
<template>
  <div class="layout">
    <div class="layout-wrapper">
      <!--  -->
      <div class="header-wrapper">
        <HeaderBar @collapseFun="collapse_fun"></HeaderBar>
      </div>
      <div class="layout-main">
        <div class="sider-bar " :class="{ 'sider-collapse':isCollapse}">
          <!--  -->
          <SideMenu :collapse="isCollapse" :active-name="$route.name" :menuList="menuList"></SideMenu>
        </div>
        <div class="layout-content" :class="{ 'content-collapse':isCollapse}">
          <div class="tag-nav-wrapper">
            <tags-nav :value="$route" @input="handleClick" :list="tagNavList" @on-close="handleCloseTag" />
          </div>
          <div class="main-content">
            <!--  -->
            <MainView>
               <router-view></router-view>
            </MainView>
          </div>
        </div>
      </div>

    </div>
    <BackTop></BackTop>

  </div>
</template>

<script>
  import HeaderBar from './components/header-bar'
  import SideMenu from './components/side-menu'
  import TagsNav from './components/tags-nav'
  import MainView from './components/main-view'
  import {
    mapMutations,
    mapActions
  } from 'vuex'
  import {
    getNewTagList,
    getNextName

  } from '@/libs/util'

  export default {
    components: {
      HeaderBar,
      SideMenu,
      TagsNav,
      MainView
    },
    data() {
      return {
        isCollapse: false
      }
    },
    computed: {
      tagNavList() {
        return this.$store.state.app.tagNavList
      },
      tagRouter() {
        return this.$store.state.app.tagRouter
      },
      userAvator() {
        return this.$store.state.user.avatorImgPath
      },
      cacheList() {
        return this.tagNavList.length ? this.tagNavList.filter(item => !(item.meta && item.meta.notCache)).map(item => item.name) : []
      },
      menuList() {
        return this.$store.getters.menuList
      }
    },
    methods: {
      collapse_fun(val) {
        this.isCollapse = val;
      },
      ...mapMutations([
        'setBreadCrumb',
        'setTagNavList',
        'addTag'
      ]),
      ...mapActions([
        'handleLogin'
      ]),
      turnToPage(name) {
        this.$router.push({
          name: name
        })
      },
      handleCollapsedChange(state) {
        this.collapsed = state
      },
      handleCloseTag(res, type, name) {
        const nextName = getNextName(this.tagNavList, name)
        this.setTagNavList(res)
        if (type === 'all') this.turnToPage('home')
        else if (this.$route.name === name) this.$router.push({
          name: nextName
        })
      },
      handleClick(item) {
        this.turnToPage(item.name)
      }
    },
    watch: {
      '$route' (newRoute) {
        this.setBreadCrumb(newRoute.matched)
        this.setTagNavList(getNewTagList(this.tagNavList, newRoute))
      }
    },
    mounted() {
      /**
       * @description 
       */
      this.setTagNavList()
      this.addTag(this.$store.state.app.homeRoute)
      this.setBreadCrumb(this.$route.matched)
    }
  };
</script>

<style scoped lang="scss">
  @import "./main";
</style>
